.ui.container {
	max-width: 750px !important;
}

#search-bar {
	flex-grow: 1;
}

.grid-list > .grid-container {
	display: flex;
	overflow: auto;
	margin-bottom: 30px;
	padding-left: 9vw;
	scroll-behavior: smooth;

	/* background: blue; */
}

.grid-list .title {
	padding-left: 9vw;
	margin-top: 0;
}

.grid-container::-webkit-scrollbar {
	height: 5px;
	display: none;
}

p {
	margin: 0;
}

.mt-5 {
	margin-top: 5px;
}
.mb-40 {
	margin-bottom: 40px;
}
.pb-20 {
	padding-bottom: 20px;
}
.pb-10 {
	padding-bottom: 10px;
}
.pt-10 {
	padding-top: 10px;
}

.pt-20 {
	padding-top: 20px;
}

.pl-10 {
	padding-left: 10px;
}

.pl-5 {
	padding-left: 5px;
}

.py-10 {
	padding-left: 10px;
}

.grid-item {
	padding-bottom: 10px;
	position: relative;
	margin-right: 16px;
	max-width: 180px;
	width: 34vw;
}

.grid-spacer img {
	opacity: 0;
	width: 9vw;
}

.grid-item > .jumper {
	position: absolute;
	width: 100%;
	height: 100%;
	transition: background 0.2s linear;
}
.grid-item > .jumper:hover {
	background: rgba(0, 0, 0, 0.2);
}

.float-right {
	float: right;
}

.control span {
	font-size: 30px;
	margin-right: 30px;
	cursor: pointer;
	outline: none;
}

.control span:hover {
	opacity: 0.5;
}

.grid-item .ui.placeholder {
	max-height: 240px;
	height: 44vw;
	width: 34vw;
	max-width: 180px;
}

.grid-item img {
	max-height: 240px;
	height: 44vw;
	width: 34vw;
	max-width: 180px;
	object-fit: cover;
	object-position: top;
	border-radius: 4px;
}

.grid-item .time {
	position: absolute;
	background-color: rgba(0, 0, 0, 0.5);
	color: white;
	padding: 3px 5px;
	font-size: 0.8em;
	right: 0;
}

.f-bold {
	font-weight: bold;
}

.ellipsis {
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

.lds-ring {
	display: inline-block;
	position: relative;
	width: 40px;
	height: 40px;
}
.lds-ring div {
	box-sizing: border-box;
	display: block;
	position: absolute;
	width: 32px;
	height: 32px;
	margin: 4px;
	border: 4px solid #4183c4;
	border-radius: 50%;
	animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
	border-color: #4183c4 transparent transparent transparent;
}
.lds-ring div:nth-child(1) {
	animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
	animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
	animation-delay: -0.15s;
}
@keyframes lds-ring {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}

.scale-05 {
	transform: scale(0.5);
}

.text-center {
	text-align: center;
}

.text-right {
	text-align: right;
}

.video-container {
	width: 100%;
	height: 100%;
	margin-bottom: 30px;
}

.series-img {
	width: 100%;
	height: 240px;
	object-fit: cover;
	object-position: top;
}

.grid-wrap {
	display: flex;
	flex-wrap: wrap;
	/* justify-content: center; */
}

.wrap-item {
	flex-basis: 25%;
	position: relative;
	transition: all 0.2s linear;
	background: white;
	height: 45vw;
	max-height: 300px;
	/* z-index: 1; */
}

.h-100 {
	height: 100% !important;
}

.wrap-item:hover {
	transform: scale(1.05);
	z-index: 2;
}

.wrap-item:hover .wrap-info {
	opacity: 1;
}

.wrap-item img {
	width: 100%;
	height: 100%;
	/* max-height: 300px; */
	object-fit: cover;
	object-position: top;
}

.wrap-item .wrap-info {
	transition: opacity 0.3s linear;
	opacity: 0;
	position: absolute;
	height: 100%;
	top: 0;
	left: 0;
	padding: 10px;
	background: rgba(0, 0, 0, 0.2);
	width: 100%;
	text-align: right;
	z-index: 1;
	color: white;
}

.img-back {
	position: fixed;
	top: 0;
	left: 0;
	z-index: -1;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: top;
	filter: blur(5px);
	opacity: 0.12;
}

@media only screen and (max-width: 600px) {
	.grid-item {
		margin-right: 8px;
		/* width: 24vw; */
	}

	.grid-spacer img {
		width: 15px;
	}

	.wrap-item {
		flex-basis: 33.3%;
	}

	.wrap-item .wrap-info {
		opacity: 1;
	}

	.hide-xs {
		display: none;
	}

	.grid-item p {
		font-size: 0.8em;
	}

	/* .grid-item img {
		height: 31vw;
		width: 24vw;
		object-fit: cover;
	} */

	.wrap-item p {
		font-size: 0.7em;
	}

	.grid-list > .grid-container {
		padding-left: 20px;
	}

	.grid-list .title {
		padding-left: 20px;
	}

	.grid-container::-webkit-scrollbar {
		display: none;
	}
}

.cursor-pointer {
	cursor: pointer;
}

.cursor-pointer:hover {
	opacity: 0.4;
}

.fixed-right {
	position: fixed;
	right: 15px;
	top: 15px;
}

.capitalize {
	text-transform: capitalize !important;
}

.fixed-top {
	position: fixed;
	top: 0;
	left: 0;
}

#app-bar {
	background: rgba(255, 255, 255, 0.96);
	padding: 7px;
	z-index: 99;
	width: 100vw;
	position: fixed;
}

.spacer {
	height: 15vw;
	max-height: 60px;
	/* width: 100%; */
}
